import * as THREE from 'three'
/**
 * Based on http://www.emagix.net/academic/mscs-project/item/camera-sync-with-css3-and-webgl-threejs
 */

const _position = new THREE.Vector3();
const _quaternion = new THREE.Quaternion();
const _scale = new THREE.Vector3();

class CustomCSS3DObject extends THREE.Object3D {

    element: HTMLElement
    followObject: THREE.Object3D | null
    followObjectOffset: THREE.Vector3

    constructor( element: HTMLElement = document.createElement( 'div' ) ) {

        super();

        this.element = element;
        this.element.style.position = 'absolute';
        this.element.style.pointerEvents = 'all';
        
        if (element.parentNode !== null) {
            element.parentNode.removeChild( element )
        }
        else {
            document.removeChild(element)
        }
        this.followObject = null
        this.followObjectOffset = new THREE.Vector3()

    }

    setFollowObject(followObject: THREE.Object3D, followObjectOffset: THREE.Vector3) {
        this.followObject = followObject
        this.followObjectOffset = followObjectOffset
    }

}


class CustomCSS3DSprite extends CustomCSS3DObject {

    rotation2D: number

    constructor( element: HTMLElement ) {

        super( element );

        this.rotation2D = 0;

    }

}

const _matrix = new THREE.Matrix4();
const _matrix2 = new THREE.Matrix4();

interface CustomCSS3DRendererCache {
    camera: {
        fov: number,
        style: string
    },
    objects: WeakMap<any, any>
}

class CustomCSS3DRenderer {

    domElement: HTMLElement
    cameraElement: HTMLElement
    _width: number
    _height: number
    _widthHalf: number
    _heightHalf: number
    cache: CustomCSS3DRendererCache

    constructor( domElement: HTMLElement ) {

        this._width = 0
        this._height = 0
        this._widthHalf = 0
        this._heightHalf = 0

        this.cache = {
            camera: { fov: 0, style: '' },
            objects: new WeakMap()
        };

        this.domElement = domElement !== undefined ? domElement : document.createElement( 'div' );

        // this.domElement.style.overflow = 'hidden';
        this.domElement.style.position = 'absolute';
        this.domElement.style.overflow = 'visible';
        this.domElement.style.pointerEvents = 'none';

        this.cameraElement = document.createElement( 'div' );
        this.cameraElement.style.overflow = 'visible';

        this.cameraElement.style.transformStyle = 'preserve-3d';
        this.cameraElement.style.pointerEvents = 'none';

        domElement.appendChild( this.cameraElement );
    }
    
    getSize () {

        return {
            width: this._width,
            height: this._height
        };

    }
    
    render ( scene: THREE.Scene, camera: THREE.PerspectiveCamera ) {

        const fov = camera.projectionMatrix.elements[ 5 ] * this._heightHalf;

        if ( this.cache.camera.fov !== fov ) {

            this.domElement.style.perspective = fov + 'px'
            this.cache.camera.fov = fov;

        }

        if ( scene.autoUpdate === true ) scene.updateMatrixWorld();
        if ( camera.parent === null ) camera.updateMatrixWorld();

        const cameraCSSMatrix = 'translateZ(' + fov + 'px)' + this.getCameraCSSMatrix( camera.matrixWorldInverse );

        const style = cameraCSSMatrix +
            'translate(' + this._widthHalf + 'px,' + this._heightHalf + 'px)';

        if ( this.cache.camera.style !== style ) {

            this.cameraElement.style.transform = style;

            this.cache.camera.style = style;

        }

        this.renderObject( scene, scene, camera, cameraCSSMatrix );

    }

    setSize ( width: number, height: number ) {

        this._width = width;
        this._height = height;
        this._widthHalf = this._width / 2;
        this._heightHalf = this._height / 2;

        this.domElement.style.width = this._width + 'px';
        this.domElement.style.height = this._height + 'px';

        this.cameraElement.style.width = this._width + 'px';
        this.cameraElement.style.height = this._height + 'px';

    }

    epsilon( value: number ) {

        return Math.abs( value ) < 1e-10 ? 0 : value;

    }

    getCameraCSSMatrix( matrix: THREE.Matrix4 ) {

        const elements = matrix.elements;

        return 'matrix3d(' +
            this.epsilon( elements[ 0 ] ) + ',' +
            this.epsilon( - elements[ 1 ] ) + ',' +
            this.epsilon( elements[ 2 ] ) + ',' +
            this.epsilon( elements[ 3 ] ) + ',' +
            this.epsilon( elements[ 4 ] ) + ',' +
            this.epsilon( - elements[ 5 ] ) + ',' +
            this.epsilon( elements[ 6 ] ) + ',' +
            this.epsilon( elements[ 7 ] ) + ',' +
            this.epsilon( elements[ 8 ] ) + ',' +
            this.epsilon( - elements[ 9 ] ) + ',' +
            this.epsilon( elements[ 10 ] ) + ',' +
            this.epsilon( elements[ 11 ] ) + ',' +
            this.epsilon( elements[ 12 ] ) + ',' +
            this.epsilon( - elements[ 13 ] ) + ',' +
            this.epsilon( elements[ 14 ] ) + ',' +
            this.epsilon( elements[ 15 ] ) +
        ')';

    }

    getObjectCSSMatrix( matrix: THREE.Matrix4 ) {

        const elements = matrix.elements;
        const matrix3d = 'matrix3d(' +
            this.epsilon( elements[ 0 ] ) + ',' +
            this.epsilon( elements[ 1 ] ) + ',' +
            this.epsilon( elements[ 2 ] ) + ',' +
            this.epsilon( elements[ 3 ] ) + ',' +
            this.epsilon( - elements[ 4 ] ) + ',' +
            this.epsilon( - elements[ 5 ] ) + ',' +
            this.epsilon( - elements[ 6 ] ) + ',' +
            this.epsilon( - elements[ 7 ] ) + ',' +
            this.epsilon( elements[ 8 ] ) + ',' +
            this.epsilon( elements[ 9 ] ) + ',' +
            this.epsilon( elements[ 10 ] ) + ',' +
            this.epsilon( elements[ 11 ] ) + ',' +
            this.epsilon( elements[ 12 ] ) + ',' +
            this.epsilon( elements[ 13 ] ) + ',' +
            this.epsilon( elements[ 14 ] ) + ',' +
            this.epsilon( elements[ 15 ] ) +
        ')';

        return matrix3d;

    }

    renderObject( object: THREE.Object3D, scene: THREE.Scene, camera: THREE.PerspectiveCamera, cameraCSSMatrix: string ) {

        if ( object instanceof CustomCSS3DObject ) {

            const visible = object.visible && object.layers.test( camera.layers );
            object.element.style.display = visible ? '' : 'none';

            // only getObjectCSSMatrix when object.visible
            if ( visible ) {

                let style;

                if (object.followObject != undefined) {
                    object.position.copy(object.followObject.position)
                    object.position.add(object.followObjectOffset)
                }

                if ( object instanceof CustomCSS3DSprite ) {

                    // http://swiftcoder.wordpress.com/2008/11/25/constructing-a-billboard-matrix/

                    _matrix.copy( camera.matrixWorldInverse );
                    _matrix.transpose();

                    if ( object.rotation2D !== 0 ) _matrix.multiply( _matrix2.makeRotationZ( object.rotation2D ) );

                    object.matrixWorld.decompose( _position, _quaternion, _scale );
                    _matrix.setPosition( _position );
                    _matrix.scale( _scale );

                    _matrix.elements[ 3 ] = 0;
                    _matrix.elements[ 7 ] = 0;
                    _matrix.elements[ 11 ] = 0;
                    _matrix.elements[ 15 ] = 1;

                    style = this.getObjectCSSMatrix( _matrix );

                } else {

                    style = this.getObjectCSSMatrix( object.matrixWorld );

                }

                const element = object.element;
                const cachedObject = this.cache.objects.get( object );

                if ( cachedObject === undefined || cachedObject.style !== style ) {

                    element.style.transform = style;

                    const objectData = { style: style };
                    this.cache.objects.set( object, objectData );

                }

                if ( element.parentNode !== this.cameraElement ) {

                    this.cameraElement.appendChild( element );

                }

            }

        }

        for ( let i = 0, l = object.children.length; i < l; i ++ ) {
            if (object.children[ i ] instanceof CustomCSS3DObject) {
                this.renderObject( object.children[ i ], scene, camera, cameraCSSMatrix );
            }
        }

    }

}

export { CustomCSS3DObject, CustomCSS3DSprite, CustomCSS3DRenderer };
